import React from "react";
import { useHistory } from "react-router-dom";
import YouTube from "react-youtube";

const exercise = {
  id: 1,
  name: "Presss con banca",
  description:
    "Recostado sobre un banco horizontal, los brazos estirados verticalmente, con la barra cargada en las manos, el movimiento consiste en bajar la barra hasta que toque el torso (fase excéntrica) y luego subir (fase concéntrica) hasta la posición inicial",
  image: "https://some image somewhere",
  video: "https://www.youtube.com/watch?v=JhcjQHkjklA",
  video_url: null
};

const ExerciseDetails = () => {
  const history = useHistory();

  const opts = {
    width: "100%"
  };

  const getVideo = () => {
    return exercise.video.split("v=")[1];
  };

  return (
    <div style={{ color: "white" }}>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          fontSize: "18px",
          alignItems: "center",
          marginBottom: "2rem"
        }}
      >
        <i
          onClick={() => history.push("/ejercicios")}
          className="fa fa-chevron-left"
          aria-hidden="true"
        ></i>
        <div
          style={{
            backgroundColor: "#c3ea4d",
            border: "2px solid #f8f9fa",
            padding: "3px 20px",
            borderRadius: "50px",
            color: "#212529"
          }}
        >
          {exercise.name}
        </div>
      </div>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <YouTube videoId={getVideo()} opts={opts} />
        <div style={{ marginBottom: "5px" }}>
          <i
            className="fa fa-circle"
            style={{ fontSize: "8px", color: "#c3ea4d", marginRight: "5px" }}
            aria-hidden="true"
          ></i>
          {`Descripción: ${exercise.description}`}
        </div>
      </div>
    </div>
  );
};

export default ExerciseDetails;
