import React from "react";
import RepsStepper from "./RepsStepper";
import { useHistory } from "react-router-dom";

const exercise = {
  id: 2,
  technique: "Desmayo",
  reps: "10,10,8,8,6",
  rest: "60-60-30-30-30",
  notes: "Hacer concentrado",
  exercise: 1,
  name: "Press con banca"
};

const Exercises = () => {
  const history = useHistory();

  const getReps = () => {
    return exercise.reps.split(",");
  };

  const getRest = () => {
    return exercise.rest.split("-");
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        color: "white"
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          marginBottom: "2rem",
          fontSize: "18px"
        }}
      >
        <i
          onClick={() => history.push("/sesiones")}
          className="fa fa-chevron-left"
          aria-hidden="true"
        ></i>
        <span
          style={{
            backgroundColor: "#c3ea4d",
            border: "2px solid #f8f9fa",
            padding: "3px 20px",
            borderRadius: "50px",
            color: "#212529"
          }}
        >
          {exercise.name}
        </span>
        <i
          onClick={() => history.push("/explicacion-ejercicio")}
          className="fa fa-question-circle"
          aria-hidden="true"
        ></i>
      </div>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <span style={{ marginBottom: "5px" }}>
          <i
            className="fa fa-circle"
            style={{ fontSize: "8px", color: "#c3ea4d", marginRight: "5px" }}
            aria-hidden="true"
          ></i>
          {`Técnica: ${exercise.technique}`}
        </span>
        <span style={{ marginBottom: "5px" }}>
          <i
            className="fa fa-circle"
            style={{ fontSize: "8px", color: "#c3ea4d", marginRight: "5px" }}
            aria-hidden="true"
          ></i>
          {`Repeticiones: ${getReps().length}`}
        </span>
        <span style={{ marginBottom: "5px" }}>
          <i
            className="fa fa-circle"
            style={{ fontSize: "8px", color: "#c3ea4d", marginRight: "5px" }}
            aria-hidden="true"
          ></i>
          {`Notas: ${exercise.notes}`}
        </span>
      </div>
      <RepsStepper reps={getReps()} rest={getRest()} />
    </div>
  );
};

export default Exercises;
